}
.containerMain{
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items:center;
    box-shadow: 0 3px 6px 0 #555;
    padding:20px 10px;
    border-radius: 4px;
    width: 380px;
    background: white;
    font-family: Montserrat;
}
.AppLabel {
    color: black;
    font-size: 20px;
    font-weight: bold;
}

.WeatherIcon {
    width: 140px;
    height: 140px;
    margin: 40px auto;
}

.CityLabel {
    color: black;
    font-size: 18px;
    font-weight: bold;
    margin: 20px auto;
}

.SearchBox {
    display: flex;
    flex-direction: row;
    border: 1px solid black;
    border-radius: 3px;
    color: black;
    margin: 20px auto;
}

.inputCustomCss {
    padding: 10px;
    font-size: 15px;
    border: none;
    outline: none;
    font-weight: bold;
}

.buttonCustomCss {
    padding: 10px;
    font-size: 15px;
    color: white;
    background-color: #34AAFF;
    border: none;
    outline: none;
    font-weight: bold;
    cursor: pointer;
}

/* weaterhinfoComponent */
.WeatherCondition {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    margin: 30px auto;
}
.Condition{
    margin: 20px auto;
    font-size: 14px;
}
.ConditionSpan{
    font-size: 28px;
}
.WeatherIcon{
    width: 100px;
    height: 100px;
    margin: 5px auto;
}
.Location{
    font-size: 28px;
  font-weight: bold;
}
.WeatherLabel{
    font-size: 14px;
  font-weight: bold;
  margin: 20px 25px 10px;
  text-align: start;
  width: 90%;
}
.WeatherInfoContainer{
    display: flex;
    width: 90%;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}
.InfoContainer{
    display: flex;
    margin: 5px 10px;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}
.InfoLabel{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    margin: 15px;
}
.InfoLabelSpan{
    font-size: 12px;
    text-transform: capitalize;
}